import React, { Component } from "react";
import { userData, storeData, signOut, displayName, profileImg } from "../Data";
import { S, wait, toEnglishNumber, Snackbar, snackbarHandle } from "../dependencies";
import { gsap } from "gsap";

let deferredPrompt;
window.addEventListener("beforeinstallprompt", (e) => (deferredPrompt = e));
function addtohome() {
  deferredPrompt.prompt();
  deferredPrompt.userChoice.then((choiceResult) => {
    if (choiceResult.outcome === "accepted") {
      S("#addToHomeSettingsContainer").css("display", "none");
      console.log("User accepted the install prompt");
    } else {
      console.log("User dismissed the install prompt");
    }
  });
}

export default class Settings extends Component {
  state = {
    kcal: userData.userInfo.dailykcal,
    carbGrams: userData.userInfo.dailyCarbs,
    protienGrams: userData.userInfo.dailyProtein,
    fatGrams: userData.userInfo.dailyFat,

    carbPercent: Math.min(Math.max(Math.round(((userData.userInfo.dailyCarbs * 4) / userData.userInfo.dailykcal) * 100), 0), 100),
    proteinPercent: Math.min(
      Math.max(Math.round(((userData.userInfo.dailyProtein * 4) / userData.userInfo.dailykcal) * 100), 0),
      100
    ),
    fatPercent: Math.min(Math.max(Math.round(((userData.userInfo.dailyFat * 9) / userData.userInfo.dailykcal) * 100), 0), 100),
  };

  componentDidMount() {
    window.scrollTo({ top: 0 });
    
    this.checkWeightGoal();
    
    if (window.localStorage.getItem("tips") === "true") {
      this.moveSwitch("on", "tips");
    }
    
    if (window.localStorage.getItem("darkMode") === "true") {
      this.moveSwitch("on", "darkmode");
      this.autoDarkModeAnimation("on");
    }
    
    if (window.localStorage.getItem("autoDarkMode") === "true") {
      this.moveSwitch("on", "autoDarkmode");
    }
    document.getElementById("addToHomeSettingsContainer").addEventListener("click", addtohome);
    
    this.centerSelect("activity");
    this.centerSelect("gender");
    
    this.setInputDefaultColor();

    document.getElementById("settingsPage").style.opacity = "1";

  }

  componentWillUnmount() {
    storeData();
  }

  checkWeightGoal = () => {
    if (userData.userInfo.goalWeight < userData.userInfo.weight) {
      S("#weightGoal").text("خسارة الوزن");
    } else if (userData.userInfo.goalWeight === userData.userInfo.weight) {
      S("#weightGoal").text("الحفاظ على الوزن");
    } else if (userData.userInfo.goalWeight > userData.userInfo.weight) {
      S("#weightGoal").text("اكتساب الوزن");
    }
  };

  centerSelect = async (id) => {
    const element = document.getElementById(id);
    const newDiv = document.createElement("p");
    newDiv.classList.add("centerSelect")
    newDiv.innerHTML = element.options[element.selectedIndex].innerHTML;
    document.body.appendChild(newDiv);
    const getWidth = parseFloat(window.getComputedStyle(newDiv).getPropertyValue("width")) / 2 + "px)";
    newDiv.remove();
    
    element.style.width = "0px";
    await wait(20);
    element.style.removeProperty("width");
    element.style.paddingRight = "calc(50% - " + getWidth;
  };

  HarrisBenedictBMR = () => {
    let bmr;
    if (userData.userInfo.gender === "male") {
      bmr = 66.47 + 13.75 * userData.userInfo.weight + 5.003 * userData.userInfo.height - 6.755 * userData.userInfo.age;
    } else if (userData.userInfo.gender === "female") {
      bmr = 655.1 + 9.563 * userData.userInfo.weight + 1.85 * userData.userInfo.height - 4.676 * userData.userInfo.age;
    }
    return Math.round(Math.round(bmr) / 100) * 100;
  };

  autoAssign = () => {
    const bmr = this.HarrisBenedictBMR();
    const goal = userData.userInfo.goalWeight;
    const weight = userData.userInfo.weight;
    const activity = userData.userInfo.activity;

    let kcal;
    let carbs;
    let protien;
    let fat;

    if (goal < weight) {
      kcal = bmr;
      carbs = Math.round((kcal * 0.5) / 4);
      protien = Math.round((kcal * 0.25) / 4);
      fat = Math.round((kcal * 0.25) / 9);
    } else if (goal === weight) {
      kcal = Math.round(bmr * activity);
      carbs = Math.round((kcal * 0.55) / 4);
      protien = Math.round((kcal * 0.2) / 4);
      fat = Math.round((kcal * 0.25) / 9);
    } else if (goal > weight) {
      kcal = Math.round(bmr * activity + 500);
      carbs = Math.round((kcal * 0.55) / 4);
      protien = Math.round((kcal * 0.2) / 4);
      fat = Math.round((kcal * 0.25) / 9);
    }
    userData.userInfo.dailykcal = kcal;
    userData.userInfo.dailyCarbs = carbs;
    userData.userInfo.dailyProtein = protien;
    userData.userInfo.dailyFat = fat;
    this.setState({
      kcal: userData.userInfo.dailykcal,
      carbGrams: userData.userInfo.dailyCarbs,
      protienGrams: userData.userInfo.dailyProtein,
      fatGrams: userData.userInfo.dailyFat,

      carbPercent: Math.min(
        Math.max(Math.round(((userData.userInfo.dailyCarbs * 4) / userData.userInfo.dailykcal) * 100), 0),
        100
      ),
      proteinPercent: Math.min(
        Math.max(Math.round(((userData.userInfo.dailyProtein * 4) / userData.userInfo.dailykcal) * 100), 0),
        100
      ),
      fatPercent: Math.min(Math.max(Math.round(((userData.userInfo.dailyFat * 9) / userData.userInfo.dailykcal) * 100), 0), 100),
    });
  };

  moveSwitch = (d, n) => {
    if (d === "on") {
      S("#" + n + "SwitchContainer").css("background-color", "var(--main-color)");
      S("#" + n + "SwitchButton")
        .css("left", "90%")
        .css("border", "solid 1px var(--main-color)");
      gsap.to("#" + n + "SettingsContainer", {
        duration: 0.2,
        background: "linear-gradient(90deg, var(--second-color)  100%, rgba(0,0,0,0) 100%)",
      });
      S("#" + n + "SettingsContainer p").css("color", "white");
    } else if (d === "off") {
      S("#" + n + "SwitchContainer")
        .css("background-color", "whitesmoke")
        .css("border", "solid 1px lightgray");
      S("#" + n + "SwitchButton")
        .css("left", "0px")
        .css("border", "solid 1px lightgray");
      gsap.to("#" + n + "SettingsContainer", {
        duration: 0.2,
        background: "linear-gradient(90deg, var(--second-color)  0%, rgba(0,0,0,0) 0%)",
      });
      S("#" + n + "SettingsContainer p").removeAttr("style");
    }
  };

  tipSwitchButton = (state) => {
    if (state === "false") {
      this.moveSwitch("on", "tips");
      window.localStorage.setItem("tips", true);
    } else {
      this.moveSwitch("off", "tips");
      window.localStorage.setItem("tips", false);
    }
  };

  darkModeOnOff = (m) => {
    if (m === "on") {
      document.documentElement.style.setProperty("--background-gray", "#18191a");
      document.documentElement.style.setProperty("--background-white", "#242526");
      document.documentElement.style.setProperty("--background-kinda-white", "#3a3b3c");
      document.documentElement.style.setProperty("--text-kinda-black", "#e4e6eb");
      document.documentElement.style.setProperty("--text-gray", "#a7aaae");
      document.documentElement.style.setProperty(
        "--select-background-image",
        `url(${require("../assets/down-arrow-white.svg")})`
      );
    } else if (m === "off") {
      document.documentElement.style.setProperty("--background-gray", "#f5f5f5");
      document.documentElement.style.setProperty("--background-white", "white");
      document.documentElement.style.setProperty("--background-kinda-white", "#fafaf5");
      document.documentElement.style.setProperty("--text-kinda-black", "#2b2b2b");
      document.documentElement.style.setProperty("--text-gray", "#646464");
      document.documentElement.style.setProperty(
        "--select-background-image",
        `url(${require("../assets/down-arrow-black.svg")})`
      );
    }
  };

  autoDarkModeAnimation = async (state) => {
    const el1 = document.getElementById("darkmodeSettingsContainer");
    const el2 = document.getElementById("autoDarkmodeSettingsContainer");
    if (state === "on") {
      const getHeight = window.getComputedStyle(el1).getPropertyValue("height");
      el2.style.borderWidth = "1.5px";
      el2.style.height = getHeight;
      el1.style.borderBottomLeftRadius = "0px";
      el1.style.borderBottomRightRadius = "0px";
    } else {
      el2.style.height = "0px";
      await wait(150);
      el2.style.borderWidth = "0px";
      el1.removeAttribute("style");
    }
  };

  darkmodeSwitchButton = (state) => {
    if (state === "false") {
      this.moveSwitch("on", "darkmode");
      this.darkModeOnOff("on");
      this.autoDarkModeAnimation("on");
      window.localStorage.setItem("darkMode", true);
    } else {
      this.moveSwitch("off", "darkmode");
      this.darkModeOnOff("off");
      this.autoDarkModeAnimation("off");
      this.moveSwitch("off", "autoDarkmode");
      window.localStorage.setItem("darkMode", false);
      window.localStorage.setItem("autoDarkMode", false);
    }
  };

  autoDarkmodeSwitchButton = (state) => {
    if (state === "false") {
      this.moveSwitch("on", "autoDarkmode");
      snackbarHandle("من الساعة 20:00 وحتى الساعة 7:00", "info");
      const date = new Date();
      const hour = date.getHours();
      if (hour >= 20 || hour <= 6) {
        this.darkModeOnOff("on");
      } else {
        this.darkModeOnOff("off");
        const that = this;
        window.setInterval(function () {
          const date = new Date();
          const getHour = date.getHours();
          if (getHour >= 20 || getHour <= 6) that.darkModeOnOff("on");
        }, 60000);
      }
      window.localStorage.setItem("autoDarkMode", true);
    } else {
      this.moveSwitch("off", "autoDarkmode");
      this.darkModeOnOff("on");
      window.localStorage.setItem("autoDarkMode", false);
    }
  };

  changeThemeColor = (c1, c2) => {
    const colors = [c1, c2];
    document.documentElement.style.setProperty("--main-color", colors[0]);
    document.documentElement.style.setProperty("--second-color", colors[1]);
    document.querySelector("meta[name=theme-color]").setAttribute("content", colors[0]);
    window.localStorage.setItem("theme", JSON.stringify(colors));
    this.setInputDefaultColor();
  };

  setInputDefaultColor = () => {
    const themeColors = JSON.parse(window.localStorage.getItem("theme"));
    S("#selectMainColor").val(themeColors[0]);
    S("#selectSecondColor").val(themeColors[1]);
  };

  render() {
    return (
      <div id="settingsPage">
        <header id="settingsPageHeader">
          <p id="settingsPageHeaderTitle">الإعــدادات</p>
        </header>
        <Snackbar />
        <div id="UserContainer">
          <div id="userPic" style={{ backgroundImage: profileImg ? `url(${profileImg})` : "" }}></div>
          <p id="userName">{displayName ? displayName : "مستخدم غير مسجل"}</p>
          <button id="logOut" onClick={signOut}>
            تسجيل الخروج
          </button>
        </div>
        <p className="containerTitles">المعلومات الشخصية</p>
        <div id="personalInfoContainer">
          <div className="settingsItemsContainer" style={{ height: "53px" }}>
            <p className="personalInfoTitles">الهدف</p>
            <p id="weightGoal"></p>
          </div>
          <div className="settingsItemsContainer">
            <p className="personalInfoTitles">الوزن المراد تحقيقه</p>
            <p className="settingsUnits">كغ</p>
            <input
              className="personalInfoInputs"
              id="goalWeight"
              autoComplete="off"
              inputMode="numeric"
              placeholder="كغ"
              onClick={(e) => e.target.select()}
              defaultValue={userData.userInfo.goalWeight}
              onChange={(e) => {
                e.target.value = toEnglishNumber(e.target.value);
                userData.userInfo.goalWeight = Number(e.target.value);
                this.checkWeightGoal();
              }}
            />
          </div>
          <div className="settingsItemsContainer">
            <p className="personalInfoTitles">الوزن الحالي</p>
            <p className="settingsUnits">كغ</p>
            <input
              className="personalInfoInputs"
              id="weight"
              autoComplete="off"
              inputMode="numeric"
              placeholder="كغ"
              onClick={(e) => e.target.select()}
              defaultValue={userData.userInfo.weight}
              onChange={(e) => {
                e.target.value = toEnglishNumber(e.target.value);
                userData.userInfo.weight = Number(e.target.value);
                this.checkWeightGoal();
              }}
            />
          </div>
          <div className="settingsItemsContainer">
            <p className="personalInfoTitles">الطول</p>
            <p className="settingsUnits">سم</p>
            <input
              className="personalInfoInputs"
              id="height"
              autoComplete="off"
              inputMode="numeric"
              placeholder="سم"
              defaultValue={userData.userInfo.height}
              onClick={(e) => e.target.select()}
              onChange={(e) => {
                e.target.value = toEnglishNumber(e.target.value);
                userData.userInfo.height = Number(e.target.value);
              }}
            />
          </div>
          <div className="settingsItemsContainer">
            <p className="personalInfoTitles">العمر</p>
            <p className="settingsUnits">سنة</p>
            <input
              className="personalInfoInputs"
              id="age"
              autoComplete="off"
              inputMode="numeric"
              placeholder="سنة"
              defaultValue={userData.userInfo.age}
              onClick={(e) => e.target.select()}
              onChange={(e) => {
                e.target.value = toEnglishNumber(e.target.value);
                userData.userInfo.age = Number(e.target.value);
              }}
            />
          </div>
          <div className="settingsItemsContainer">
            <p className="personalInfoTitles">الجنس</p>
            <select
              id="gender"
              className="personalInfoInputs"
              defaultValue={userData.userInfo.gender}
              onChange={(e) => {
                userData.userInfo.gender = e.target.value;
                this.centerSelect("gender");
              }}
            >
              <option value="male">ذكر</option>
              <option value="female">أنثى</option>
            </select>
          </div>
          <div className="settingsItemsContainer">
            <p className="personalInfoTitles">مستوى النشاط</p>
            <select
              id="activity"
              className="personalInfoInputs"
              defaultValue={userData.userInfo.activity}
              onChange={(e) => {
                userData.userInfo.activity = e.target.value;
                this.centerSelect("activity");
              }}
            >
              <option value={1.2}>كثير الجلوس</option>
              <option value={1.375}>قليل النشاط</option>
              <option value={1.55}>متوسط النشاط</option>
              <option value={1.725}>كثير النشاط</option>
              <option value={1.9}>عالي النشاط</option>
            </select>
          </div>
        </div>

        <p className="containerTitles">إعــدادت الـتـغـذيـة</p>
        <div id="macrosInfoContainer">
          <div className="settingsItemsContainer">
            <p className="personalInfoTitles">السعرات اليومية</p>
            <p className="settingsUnits">سعرة</p>
            <input
              id="dailykcal"
              className="personalInfoInputs"
              autoComplete="off"
              inputMode="numeric"
              placeholder="سعرة"
              onClick={(e) => e.target.select()}
              value={this.state.kcal}
              onChange={(e) => {
                e.target.value = toEnglishNumber(e.target.value);
                userData.userInfo.dailykcal = Number(e.target.value);
                userData.userInfo.dailyCarbs = Math.round((Number(e.target.value) * this.state.carbPercent) / 100 / 4);
                userData.userInfo.dailyProtein = Math.round((Number(e.target.value) * this.state.proteinPercent) / 100 / 4);
                userData.userInfo.dailyFat = Math.round((Number(e.target.value) * this.state.fatPercent) / 100 / 9);

                this.setState({
                  kcal: userData.userInfo.dailykcal,
                  carbGrams: userData.userInfo.dailyCarbs,
                  protienGrams: userData.userInfo.dailyProtein,
                  fatGrams: userData.userInfo.dailyFat,
                });
              }}
            />
          </div>
          <div id="macrosContainer">
            <div id="macrosCarbPrecent" style={{ width: this.state.carbPercent + "%" }}>
              {this.state.carbPercent + "%"}
            </div>
            <div id="macrosProteinPrecent" style={{ width: this.state.proteinPercent + "%" }}>
              {this.state.proteinPercent + "%"}
            </div>
            <div id="macrosFatPrecent" style={{ width: this.state.fatPercent + "%" }}>
              {this.state.fatPercent + "%"}
            </div>
          </div>
          <div id="carbContainer">
            <div className="macroSettingsFlexContainer">
              <p id="carbTitle" className="macrosPercentTitles">
                الكربوهيدرات
              </p>
              <div style={{ width: "15%", display: "flex" }}>
                %
                <input
                  id="carbInput"
                  className="macrosPercentInputs"
                  autoComplete="off"
                  inputMode="numeric"
                  onClick={(e) => e.target.select()}
                  value={this.state.carbPercent}
                  onChange={(e) => {
                    e.target.value = Math.min(
                      Math.max(toEnglishNumber(e.target.value), 0),
                      100 - (this.state.fatPercent + this.state.proteinPercent)
                    );
                    userData.userInfo.dailyCarbs = Math.round((this.state.kcal * (Number(e.target.value) / 100)) / 4);
                    console.log(this.state.kcal);
                    this.setState({
                      carbPercent: Number(e.target.value),
                      carbGrams: userData.userInfo.dailyCarbs,
                    });
                  }}
                />
              </div>
              <p id="carbKcal" className="macrosPercentKcal">
                {this.state.carbGrams * 4}
                <span className="macrosPercentSpan"> سعرة</span>
              </p>
              <p id="carbGram" className="macrosPercentGram">
                {this.state.carbGrams}
                <span className="macrosPercentSpan"> غ</span>
              </p>
            </div>
            <input
              type="range"
              min="0"
              max="100"
              id="carbSlider"
              className="settingSlider"
              value={this.state.carbPercent}
              onChange={(e) => {
                e.target.value = Math.min(
                  Math.max(toEnglishNumber(e.target.value), 0),
                  100 - (this.state.fatPercent + this.state.proteinPercent)
                );
                userData.userInfo.dailyCarbs = Math.round((this.state.kcal * Number(e.target.value)) / 100 / 4);
                this.setState({
                  carbPercent: Number(e.target.value),
                  carbGrams: userData.userInfo.dailyCarbs,
                });
              }}
            />
          </div>

          <div id="proteinContainer">
            <div className="macroSettingsFlexContainer">
              <p id="proteinTitle" className="macrosPercentTitles">
                الــبـــروتــيـــن
              </p>
              <div style={{ width: "15%", display: "flex" }}>
                %
                <input
                  id="proteinInput"
                  className="macrosPercentInputs"
                  autoComplete="off"
                  inputMode="numeric"
                  onClick={(e) => e.target.select()}
                  value={this.state.proteinPercent}
                  onChange={(e) => {
                    e.target.value = Math.min(
                      Math.max(toEnglishNumber(e.target.value), 0),
                      100 - (this.state.carbPercent + this.state.fatPercent)
                    );
                    userData.userInfo.dailyProtein = Math.round((this.state.kcal * Number(e.target.value)) / 100 / 4);
                    this.setState({
                      proteinPercent: Number(e.target.value),
                      protienGrams: userData.userInfo.dailyProtein,
                    });
                  }}
                />
              </div>
              <p id="proteinKcal" className="macrosPercentKcal">
                {this.state.protienGrams * 4}
                <span className="macrosPercentSpan"> سعرة</span>
              </p>
              <p id="proteinGram" className="macrosPercentGram">
                {this.state.protienGrams}
                <span className="macrosPercentSpan"> غ</span>
              </p>
            </div>
            <input
              type="range"
              min="0"
              max="100"
              id="proteinSlider"
              className="settingSlider"
              value={this.state.proteinPercent}
              onChange={(e) => {
                e.target.value = Math.min(
                  Math.max(toEnglishNumber(e.target.value), 0),
                  100 - (this.state.carbPercent + this.state.fatPercent)
                );
                userData.userInfo.dailyProtein = Math.round((this.state.kcal * Number(e.target.value)) / 100 / 4);
                this.setState({
                  proteinPercent: Number(e.target.value),
                  protienGrams: userData.userInfo.dailyProtein,
                });
              }}
            />
          </div>

          <div id="fatContainer">
            <div className="macroSettingsFlexContainer">
              <p id="fatTitle" className="macrosPercentTitles">
                الـــــدهــــــون
              </p>
              <div style={{ width: "15%", display: "flex" }}>
                %
                <input
                  id="fatInput"
                  className="macrosPercentInputs"
                  autoComplete="off"
                  inputMode="numeric"
                  onClick={(e) => e.target.select()}
                  value={this.state.fatPercent}
                  onChange={(e) => {
                    e.target.value = Math.min(
                      Math.max(toEnglishNumber(e.target.value), 0),
                      100 - (this.state.carbPercent + this.state.proteinPercent)
                    );
                    userData.userInfo.dailyFat = Math.round((this.state.kcal * Number(e.target.value)) / 100 / 9);
                    this.setState({
                      fatPercent: Number(e.target.value),
                      fatGrams: userData.userInfo.dailyFat,
                    });
                  }}
                />
              </div>
              <p id="fatKcal" className="macrosPercentKcal">
                {this.state.fatGrams * 9}
                <span className="macrosPercentSpan"> سعرة</span>
              </p>
              <p id="fatGram" className="macrosPercentGram">
                {this.state.fatGrams}
                <span className="macrosPercentSpan"> غ</span>
              </p>
            </div>
            <input
              type="range"
              min="0"
              max="100"
              id="fatSlider"
              className="settingSlider"
              value={this.state.fatPercent}
              onChange={(e) => {
                e.target.value = Math.min(
                  Math.max(toEnglishNumber(e.target.value), 0),
                  100 - (this.state.carbPercent + this.state.proteinPercent)
                );
                userData.userInfo.dailyFat = Math.round((this.state.kcal * Number(e.target.value)) / 100 / 9);
                this.setState({
                  fatPercent: Number(e.target.value),
                  fatGrams: userData.userInfo.dailyFat,
                });
              }}
            />
          </div>
          <button id="autoAssign" onClick={this.autoAssign}>
            تـعـيـيـن تـلـقـائـي
          </button>
        </div>

        <p className="containerTitles">إعــدادت الـتـطـبـيـق</p>
        <div id="appSettingsContainer">
          <div id="tipsSettingsContainer">
            <p>إظــهــار نــصــائــح عــامــة</p>
            <div id="tipsSwitchContainer" onClick={() => this.tipSwitchButton(window.localStorage.getItem("tips"))}>
              <div id="tipsSwitchButton"></div>
            </div>
          </div>

          <div id="darkmodeSettingsContainer">
            <p>تـشغـيـل الـوضـع الـلـيـلـي</p>
            <div id="darkmodeSwitchContainer" onClick={() => this.darkmodeSwitchButton(window.localStorage.getItem("darkMode"))}>
              <div id="darkmodeSwitchButton"></div>
            </div>
          </div>

          <div id="autoDarkmodeSettingsContainer">
            <p>تغعيل تلقائي للوضـع الليلي</p>
            <div
              id="autoDarkmodeSwitchContainer"
              onClick={() => this.autoDarkmodeSwitchButton(window.localStorage.getItem("autoDarkMode"))}
            >
              <div id="autoDarkmodeSwitchButton"></div>
            </div>
          </div>

          <div className="settingsItemsContainer" style={{ paddingTop: "3%" }}>
            <p className="personalInfoTitles">اخــتــر ألـوان الـتـطـبـيـق</p>
            <div style={{ display: "flex", justifyContent: "space-evenly" }}>
              <div
                className="themeColorContainer"
                style={{ background: "linear-gradient(to right, #41cd8c, #45d0dd)" }}
                onClick={() => this.changeThemeColor("#41cd8c", "#45d0dd")}
              ></div>
              <div
                className="themeColorContainer"
                style={{ background: "linear-gradient(to right, #517fa4, #243949)" }}
                onClick={() => this.changeThemeColor("#517fa4", "#243949")}
              ></div>
              <div
                className="themeColorContainer"
                style={{ background: "linear-gradient(to right, #099a97, #15cda8)" }}
                onClick={() => this.changeThemeColor("#099a97", "#15cda8")}
              ></div>
              <div
                className="themeColorContainer"
                style={{ background: "linear-gradient(to right, #e52d27  , #b31217)" }}
                onClick={() => this.changeThemeColor("#e52d27", "#b31217")}
              ></div>
            </div>
            <div style={{ display: "flex", justifyContent: "space-evenly" }}>
              <div
                className="themeColorContainer"
                style={{ background: "linear-gradient(to right, #799F0C, #ACBB78)" }}
                onClick={() => this.changeThemeColor("#799F0C", "#ACBB78")}
              ></div>
              <div
                className="themeColorContainer"
                style={{ background: "linear-gradient(to right, #9e6496, #42275a)" }}
                onClick={() => this.changeThemeColor("#9e6496", "#42275a")}
              ></div>
              <div
                className="themeColorContainer"
                style={{ background: "linear-gradient(to right, #c71d6f, #fd68a3)" }}
                onClick={() => this.changeThemeColor("#c71d6f", "#fd68a3")}
              ></div>
              <div
                className="themeColorContainer"
                style={{ background: "linear-gradient(to right, #ed6ea0, #ec8c69)" }}
                onClick={() => this.changeThemeColor("#ed6ea0", "#ec8c69")}
              ></div>
            </div>
            <div id="customThemeColor">
              <p className="personalInfoTitles">لون مخصص</p>
              <input
                id="selectSecondColor"
                type="color"
                onChange={(e) => {
                  const colors = JSON.parse(window.localStorage.getItem("theme"));
                  colors[1] = e.target.value;
                  document.documentElement.style.setProperty("--second-color", colors[1]);
                  window.localStorage.setItem("theme", JSON.stringify(colors));
                }}
              />
              <input
                id="selectMainColor"
                type="color"
                onChange={(e) => {
                  const colors = JSON.parse(window.localStorage.getItem("theme"));
                  colors[0] = e.target.value;
                  document.documentElement.style.setProperty("--main-color", colors[0]);
                  document.querySelector("meta[name=theme-color]").setAttribute("content", colors[0]);
                  window.localStorage.setItem("theme", JSON.stringify(colors));
                }}
              />
            </div>
          </div>

          <div id="addToHomeSettingsContainer" style={{ display: deferredPrompt ? "block" : "none" }}>
            <p>الـتـثـبـيت كـتـطبـيـق ويـب</p>
          </div>

          <div
            id="shareSettingsContainer"
            onClick={() => {
              if (navigator.share) {
                navigator
                  .share({
                    title: "سعراتي اليومية",
                    text: document.querySelector("title").innerHTML,
                    url: window.location.origin,
                  })
                  .then(() => console.log("Successful share"))
                  .catch((error) => console.log("Error sharing", error));
              }
            }}
            style={{ display: navigator.share ? "block" : "none" }}
          >
            <p>مـشـاركـة رابـط الـتـطـبـيـق</p>
          </div>
        </div>
      </div>
    );
  }
}
